import React from 'react'
import {ACTIONS} from './useReducerTodo'

export default function useReducerTodoItem({todo, dispatch}) {
    return (
        <div>
            <span style={{color: todo.complete ? '#00F' : '#FF0' }}>{todo.name}</span>
            <button onClick={() => dispatch({
                type: ACTIONS.TOGGLE_TODO, 
                payload: {id: todo.id}})}>Toggle</button>
                
            <button onClick={() => dispatch({
                type: ACTIONS.DELETE_TODO, 
                payload: {id: todo.id}})}>Delete</button>
        </div>
    )
}
 